	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Dead-End Job","date":"19 Oct 2004","slug":"/2004-10-19-deadend-job/","tags":"Y2004"},"html":"<p>Maybe it's just me, but I would think that being laid off by General Motors would be a good chance for me to take stock of my life, leave a dead-end industry, and find a real job. Although I guess the fact that I was working for General Motors would be a sign I hadn't thought real hard about my future. Like, say, <a title=\"Yahoo! News - Opel management, workers set for head-on clash as work stoppages continue\" href=\"http://news.yahoo.com/news?tmpl=story&u=/afp/20041018/bs_afp/germany_us_auto_strike_041018235539\">the wildcat strikers at the Opel factory in Bochum</a>.</p>\n<p>Then again, my present job doesn't really have much of a future either, so what do I know?</p>"},"next":{"fields":{"title":"U better get busy if U wanna get paid","date":"18 Oct 2004","slug":"/2004-10-18-u-better-get-busy-if-u-wanna-get-paid/"}},"previous":{"fields":{"title":"Enough with the US election","date":"19 Oct 2004","slug":"/2004-10-19-enough-with-the-us-election/"}}},{"node":{"fields":{"title":"U better get busy if U wanna get paid","date":"18 Oct 2004","slug":"/2004-10-18-u-better-get-busy-if-u-wanna-get-paid/","tags":"Y2004"},"html":"<p>If anyone is keeping track, I still haven't received my September salary (although the firm has made a &euro;1000 partial payment to each employee). Late payments... this is starting to feel like free-lance work, only without the freedom.</p>"},"next":{"fields":{"title":"Spampot","date":"18 Oct 2004","slug":"/2004-10-18-spampot/"}},"previous":{"fields":{"title":"Dead-End Job","date":"19 Oct 2004","slug":"/2004-10-19-deadend-job/"}}},{"node":{"fields":{"title":"Spampot","date":"18 Oct 2004","slug":"/2004-10-18-spampot/","tags":"Y2004"},"html":"<p>Last Tuesday I quietly installed <a href=\"http://www.jayallen.org/projects/mt-blacklist/\">MT-Blacklist</a> and opened comments on all of my previous blog entries. The first spam arrived the next day. The statistics for the past 5 days are:<br />\n* Comment spams blocked:  \t 285<br />\n* Comment spams moderated: \t3 (of which 2 were truly spam)<br />\n* Comment spams posted: 0</p>"},"next":{"fields":{"title":"CSS tinkering: font size","date":"18 Oct 2004","slug":"/2004-10-18-css-tinkering-font-size/"}},"previous":{"fields":{"title":"U better get busy if U wanna get paid","date":"18 Oct 2004","slug":"/2004-10-18-u-better-get-busy-if-u-wanna-get-paid/"}}}],"pathPrefix":"","first":false,"last":false,"index":400,"pageCount":1101}}

